.navbar {
  width: 100%;
  height: 58px;
  font-family: PingFang-SC-Bold;
  font-size: 18px;
  color: #515060; }
  .navbar .am-navbar-title {
    font-family: PingFang-SC-Bold;
    font-size: 18px;
    color: #333; }
  .navbar .commonBack {
    cursor: pointer;
    position: absolute;
    left: 0;
    top: 0;
    width: 40px;
    height: 56px;
    background-image: url("../../assets/images/new/icon-back.svg");
    background-repeat: no-repeat;
    background-position: center center; }

.am-tabs-tab-bar-wrap {
  padding: 0 41px; }

.am-tabs-default-bar-top .am-tabs-default-bar-tab:after {
  background-color: #fff; }

html:not([data-scale]) .am-tabs-default-bar-top .am-tabs-default-bar-tab:after {
  background-color: #fff; }

#m-tabs-0-1 {
  padding: 0 10px; }

.bankRecordContainer {
  overflow: hidden; }
  .bankRecordContainer .nodata {
    text-align: center;
    font-size: 14px;
    color: #888998;
    font-family: PingFangSC-Regular;
    height: 100%;
    position: relative; }
    .bankRecordContainer .nodata span {
      display: block;
      position: absolute;
      width: 100%;
      left: 0;
      top: 40%;
      margin-top: -8px; }

.rentListContent {
  padding: 10px 18px 40px;
  height: 540px;
  overflow: auto; }
  .rentListContent::-webkit-scrollbar {
    /*滚动条整体样式*/
    width: 4px;
    /*高宽分别对应横竖滚动条的尺寸*/
    height: 4px; }
  .rentListContent::-webkit-scrollbar-thumb {
    /*滚动条里面小方块*/
    border-radius: 4px;
    background: #C2C8D5; }
  .rentListContent::-webkit-scrollbar-track {
    /*滚动条里面轨道*/
    background: transparent; }
  .rentListContent .noData {
    text-align: center;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #505160;
    margin: 10px 0; }
